/*헤더*/
.app_header {
  width: 100%;
  height: 10vw;
  padding: 5% 5% 0 5%;
  box-sizing: border-box;
  background-color: #FEF9A7;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.app_header>h1 {
  margin: 0;
  font-size: 3vw;
  /* font-weight: lighter; */
  font-style: italic;
  color: black;
}

.app_header>nav {
  width: 40%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 2vw;
  /* font-weight: lighter; */
  color: black;
  letter-spacing: 1px;
  cursor: pointer;
}

@media screen and (min-width:768px) {
  .app_header {
    background-color: mediumslateblue;
    height: 5vw;
  }
}

section {
  width: 100%;
}

h2 {
  display: none;
}

/*섹션1*/
.sec_intro {
  background-color: #FEF9A7;
  height: 60vw;
  padding: 15% 5% 0 5%;
  box-sizing: border-box;
}

.sec_intro>p {
  /* background-color: darksalmon; */
  margin: 0;
  width: 100%;
  height: 100%;
  line-height: 9vw;
  font-size: 4vw;
  /* font-weight: bolder; */
  color: black;
  letter-spacing: 8px;
}

/*섹션2*/
.sec_projects {
  background-color: #fff;
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}

h3 {
  /* background-color: mediumslateblue; */
  margin: 0;
  width: 100%;
  height: 20%;
  padding: 8% 0 0 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 3vw;
  font-weight: normal;
  letter-spacing: 1px;
}

.cover_projects {
  /* background-color: rosybrown; */
  margin: 10% 0 0 0;
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cover_content {
  /* background-color: rgba(25, 25, 112, 0.533); */
  margin: auto;
  width: 30%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;  
  border: 2px solid #cccccc40;
  border-radius: 3px;
  box-sizing: border-box;
  box-shadow: 5px 5px 10px 1px #ccc;
  cursor: pointer;
}

.cover_content>img{
  width: 100%;
  height: 35%;
  display: block;
}
/*섹션3*/
.sec_about {
  background-color: aliceblue;
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}

.cover_about{
  /* margin: 5% 0 0 0; */
  width: 100%;
  height: 70%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.cover_about>div{
  background-color: orange;
  width: 35%;
  height: 100%;
}
.cover_about>ul{
  background-color: #FEF9A7;
  margin: 0;
  padding: 0;
  width: 65%;
}
.cover_about>ul>li{
  margin: 0;
  list-style: none;
}

/*섹션4*/
.sec_contact {
  background-color: olive;
  height: 40vw;
  padding: 0 0.5%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cover_contact{
  /* background-color: cornsilk; */
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.cover_contact>div{
  width: 30%;
  height: 90%;
  border-top: 2px solid darkgreen;
  border-bottom: 2px solid darkgreen;
  box-sizing: border-box;
}

/*풋터*/
.app_footer {
  background-color: #FEF9A7;
  width: 100%;
  height: 5vw;
  text-align: center;
  font-size: 1.5vw;
  line-height: 5vw;
  color: rgba(0, 0, 0, 0.5);
}